@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  /* background: url("https://media.istockphoto.com/vectors/seamless-italian-pizza-pattern-on-black-background-vintage-pizza-vector-id1137028124?k=6&m=1137028124&s=612x612&w=0&h=5RQTk8_WBottBuDG_0ehMONtU1VA4ISWLB_fpcsfPCM=") */
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background: white;
  height: auto;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  border: solid 5px #fff;
  color: #DA0037;
}

.body {
  padding: 100px;

}

img{
  width: 100%;
  height: 250px;
}

.order{
  position: fixed;
  right: 100px;
}

.Modal{
position: fixed;
left: 0;
top: 0;
right:0;
bottom: 0;
background-color: rgba(0, 0, 0, 0.5);
display: -webkit-flex;
display: flex;
-webkit-align-items: center;
        align-items: center;
-webkit-justify-content: center;
        justify-content: center;
}

.Modal-content {
  width: 500px;
  background-color: #558776;
}

.Modal-header, .Modal-footer {
  padding: 10px;
}

.Modal-title {
  margin: 0;
}

.Modal-body {
  padding: 10px;
  border-top: 1px solid gray;
  border-bottom: 1px solid gray;
}

h5 {
  color: #558776;
}

.line-through{
  text-decoration: line-through;
}

